<template>
  <main class="box">
    <h2></h2>
    <div class="category-topBtn" style="margin-top: 20px">
      <el-button @click="delCategory()"></el-button>
    </div>
    <el-table stripe border ref="multipleTable" :data="contact.data" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column label="id" width="220">
        <template slot-scope="scope">{{ scope.row._id }}</template>
      </el-table-column>
      <el-table-column prop="createTime" label="" width="100">
      </el-table-column>
      <el-table-column prop="name" label="" width="100">
      </el-table-column>
      <el-table-column prop="phone" label="" width='130'>
      </el-table-column>
      <el-table-column prop="content" label="" show-overflow-tooltip>
      </el-table-column>
      <el-table-column fixed="right" label="" width="80">
        <template slot-scope="scope">
          <!--<el-button size="small" @click="addCategory(scope.row.id)"></el-button>-->
          <el-button type="danger" size="small" @click="delCategory(scope.row._id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background layout="prev, pager, next" :page-size="contact.limit" @current-change="handleCurrentChange" :total="contact.total">
    </el-pagination>
    <!--<el-dialog-->
    <!--:close-on-click-modal= false-->
    <!--class="addCategory-box" title="" :visible.sync="addCategoryBox">-->
    <!--<el-form :model="form">-->
    <!--<el-form-item label="" :label-width="formLabelWidth">-->
    <!---->
    <!--</el-form-item>-->
    <!--<el-form-item label="" :label-width="formLabelWidth">-->
    <!--<div class="quill-wrap">-->
    <!--<quill-editor-->
    <!--v-model="content"-->
    <!--ref="myQuillEditor"-->
    <!--:options="editorOption"-->
    <!--&gt;-->
    <!--</quill-editor>-->
    <!--</div>-->
    <!--</el-form-item>-->
    <!--</el-form>-->
    <!--<div slot="footer" class="dialog-footer">-->
    <!--<el-button @click="addCategoryBox = false"> </el-button>-->
    <!--<el-button type="primary" @click="addSubmit"> </el-button>-->
    <!--</div>-->
    <!--</el-dialog>-->
  </main>
</template>
<script>
// import {quillEditor, Quill} from 'vue-quill-editor'
// import {ImageExtend, QuillWatch} from 'quill-image-extend-module'

// Quill.register('modules/ImageExtend', ImageExtend)
export default {
  layout: 'admin',
  scrollToTop: true,
  // components: {quillEditor},
  name: 'contact',
  data() {
    return {
      contact: {},
      multipleSelection: [],
      addCategoryBox: false, // 
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      formLabelWidth: '100px'
      // content: '',
      // // 
      // editorOption: {
      //   modules: {
      //     ImageExtend: {
      //       loading: true, //  
      //       name: 'img', // 
      //       size: 3, //  M1M = 1024kb
      //       action: '', // , actionbase64
      //       // response 
      //       // {code: 200; data:{ url: 'baidu.com'}}
      //       //  return res.data.url
      //       response: (res) => {
      //         return res
      //       },
      //       headers: (xhr) => {}, //  
      //       start: () => {}, //  
      //       end: () => {}, //  
      //       error: () => {}, //  
      //       change: (xhr, formData) => {} //  headersformData
      //     },
      //     toolbar: {
      //       container: [
      //         [{ 'size': ['small', false, 'large'] }],
      //         ['bold', 'italic'],
      //         [{ 'list': 'ordered' }, { 'list': 'bullet' }],
      //         ['link', 'image']
      //       ], // container
      //       handlers: {
      //         'image': function () { // 
      //           QuillWatch.emit(this.quill.id)
      //         }
      //       }
      //     }
      //   }
      // }
    }
  },
  head() {
    return {
      title: ''
    }
  },
  created() {
    this.$api.get(this.$root.urlPath.MJK + '/contact', {}, res => {
      console.log(res.data)
      if (res.data.code === 200) {
        this.contact = res.data.data
      } else {
        this.$message.error(res.data.msg)
      }
    })

  },
  methods: {
    // 
    handleCurrentChange(e) {
      // e 
      // console.log(e)
      this.$api.get(this.$root.urlPath.MJK + '/contact', {
        current_page: e
      }, res => {
        console.log(res.data)
        if (res.data.code === 200) {
          this.contact = res.data.data
        } else {
          this.$message.error(res.data.msg)
        }
      })

    },
    // 
    // addCategory (e) {
    //   this.addCategoryBox = !this.addCategoryBox
    // },
    // // 
    // addSubmit () {
    //   this.addCategoryBox = !this.addCategoryBox
    // },
    // 
    delCategory(id) {
      let ids = []
      if (!id) {
        if (this.multipleSelection.length === 0) {
          this.$message({
            message: '',
            type: 'warning'
          })
          return
        }
        for (let i of this.multipleSelection) {
          ids.push(i._id)
        }
      } else {
        // console.log('' + id)
        ids.push(id)
      }

      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$api.delete(this.$root.urlPath.MJK + '/contact', {
          id: ids
        }, res => {
          console.log(res.data)
          // console.log(res)
          if (res.data.code === 200) {
            this.$message({
              type: 'success',
              message: '!'
            })
            this.$api.get(this.$root.urlPath.MJK + '/contact', {

            }, res => {
              // console.log(res)
              if (res.data.code === 200) {
                this.contact = res.data.data
              } else {
                this.$message.error(res.data.msg)
              }
            })
          } else {
            this.$message({
              message: res.data.msg,
              type: 'warning'
            })
          }
        })

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val
      console.log(val)
    },
    // 
    handleSuccess(response, file, fileList) {
      console.log(response, file, fileList)
    },
    // 
    handleError(err, file, fileList) {
      console.log(err, file, fileList)
    },
    // 
    handleRemove(file, fileList) {
      console.log(file, fileList)
    }
  }
}

</script>
<style scoped>
.category-topBtn {
  margin: 20px 0;
}

.el-pagination {
  margin-top: 20px;
  text-align: right;
}

</style>
<style>
.ql-editor {
  min-height: 200px;
  max-height: 400px;
  overflow-y: auto;
}

.ql-snow .ql-picker.ql-size .ql-picker-label::before,
.ql-snow .ql-picker.ql-size .ql-picker-item::before,
.ql-snow .ql-picker.ql-header .ql-picker-label::before,
.ql-snow .ql-picker.ql-header .ql-picker-item::before,
.ql-snow .ql-picker.ql-font .ql-picker-label::before,
.ql-snow .ql-picker.ql-font .ql-picker-item::before {
  position: relative;
  top: -7px;
}

</style>



