<template>
  <main class="box">
    <h2> <small></small></h2>
    <div class="category-topBtn" style="margin-top: 20px">
      <el-button @click="addCategory()"></el-button>
      <el-button @click="delCategory()"></el-button>
    </div>
    <div class="category-topBtn" style="margin-top: 20px">
      <el-button :type="cActive === -1?'primary':''" @click="selectCategory()"></el-button>
      <el-button :type="cActive === item._id?'primary':''" @click="selectCategory(item._id)" v-for="item in category" class="category-item" :key="item._id">{{item.name}}</el-button>
    </div>
    <el-table
      stripe
      border
      ref="multipleTable"
      :data="goods.data"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        label="id"
        width="230">
        <template slot-scope="scope">{{ scope.row._id }}</template>
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="date"
        label=""
        width="200">
        <template slot-scope="scope">{{ scope.row.createTime }}</template>
      </el-table-column>

      <el-table-column
        prop="hot"
        label=""
        width="200">
      </el-table-column>
      <el-table-column
        fixed="right"
        label=""
        width="200">
        <template slot-scope="scope">
          <el-button size="small" @click="addCategory(scope.row._id)"></el-button>
          <el-button type="danger" size="small" @click="delCategory(scope.row._id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="goods.limit"
      @current-change="handleCurrentChange"
      :total="goods.total">
    </el-pagination>

    <el-dialog
      :close-on-click-modal= false
      :fullscreen= true
      class="addCategory-box" title="" :visible.sync="addCategoryBox">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" placeholder="" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.category" placeholder="">
            <el-option
              v-for="item in category"
              :key="item._id"
              :label="item.name"
              :value="item._id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-upload
            action="https://kc.hcode.xyz/api/upload"
            list-type="picture-card"
            :file-list="fileList"
            :on-success="handleSuccess"
            :on-error="handleError"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove">
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <div class="quill-wrap">
            <quill-editor
              v-model="form.content"
              ref="myQuillEditor"
              :options="editorOption"
            >
            </quill-editor>
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addCategoryBox = false"> </el-button>
        <el-button type="primary" @click="addSubmit"> </el-button>
      </div>
    </el-dialog>

  </main>
</template>

<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor, Quill } from 'vue-quill-editor'
import { ImageExtend, QuillWatch } from 'quill-image-extend-module'

Quill.register('modules/ImageExtend', ImageExtend)
export default {
  components: { quillEditor },
  layout: 'admin',
  scrollToTop: true,
  name: 'goods',
  data () {
    return {
      cActive: -1, // 
      category: [], // 
      goods: [], // 
      multipleSelection: [],
      addCategoryBox: false, // 
      form: {
        name: '', // 
        id: '', // id
        category: '', // 
        img: [], // 
        content: '' // 
      },
      fileList: [
        // {
        //   name: 'food.jpeg',
        //   url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg'
        // }
      ], // 
      formLabelWidth: '100px',
      dialogImageUrl: '',
      dialogVisible: false,
      // 
      editorOption: {
        modules: {
          ImageExtend: {
            loading: true, //  
            name: 'file', // 
            size: 3, //  M1M = 1024kb
            action: 'https://kc.hcode.xyz/api/upload', // , actionbase64
            // response 
            // {code: 200; data:{ url: 'baidu.com'}}
            //  return res.data.url
            response: res => {
              return res.data.url
            },
            headers: xhr => {}, //  
            start: () => {}, //  
            end: () => {}, //  
            error: () => {}, //  
            change: (xhr, formData) => {} //  headersformData
          },
          toolbar: {
            container: [
              [{ size: ['small', false, 'large'] }],
              ['bold', 'italic'],
              [{ list: 'ordered' }, { list: 'bullet' }],
              ['link', 'image']
            ], // container
            handlers: {
              image: function () {
                // 
                QuillWatch.emit(this.quill.id)
              }
            }
          }
        }
      }
    }
  },
  created () {
    this.$api.get(this.$root.urlPath.MJK + '/category', {}, res => {
      // console.log(res.data)
      if (res.data.code === 200) {
        this.category = res.data.data.category
      } else {
        this.$message.error(res.data.msg)
      }
    })
    this.$api.get(this.$root.urlPath.MJK + '/goods', {}, res => {
      // console.log(res.data)
      if (res.data.code === 200) {
        this.goods = res.data.data
      } else {
        this.$message.error(res.data.msg)
      }
    })
  },
  head () {
    return {
      title: ''
    }
  },
  methods: {
    // 
    selectCategory (id) {
      console.log(id)
      this.cActive = id
      this.$api.get(this.$root.urlPath.MJK + '/goods', {
        categoryId: id
      }, res => {
      // console.log(res.data)
        if (res.data.code === 200) {
          this.goods = res.data.data
          this.$message({
            message: '',
            type: 'success'
          })
        } else {
          this.$message.error(res.data.msg)
          this.goods = []
        }
      })
    },
    // 
    handleCurrentChange (e) {
      // e 
      // console.log(e)
      this.$api.get(this.$root.urlPath.MJK + '/goods', {
        current_page: e

      }, res => {
      // console.log(res.data)
        if (res.data.code === 200) {
          this.goods = res.data.data
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    // 
    addCategory (e) {
      this.addCategoryBox = !this.addCategoryBox
      // console.log(e)
      if (e) {
        // 
        // console.log(e)
        let form = this.form
        this.$api.get(this.$root.urlPath.MJK + '/goods/detail', {
          id: e

        }, res => {
          // console.log(res.data)
          if (res.data.code === 200) {
            form.img = res.data.data[0].img
            form.name = res.data.data[0].name
            form.content = res.data.data[0].content
            form.category = res.data.data[0].categoryId
            form.id = e
            this.form = form
            this.fileList = res.data.data[0].img
          } else {
            this.$message.error(res.data.msg)
          }
        })
      } else {
        this.form = {
          name: '', // 
          id: '', // id
          category: '', // 
          img: [], // 
          content: '' // 
        }
        this.fileList = [] // 
      }
    },
    // 
    addSubmit () {
      let id = this.form.id
      let name = this.form.name
      let img = this.form.img
      let content = this.form.content
      let category = this.form.category
      if (!name) {
        this.$message.error('')
        return
      }
      if (!category) {
        this.$message.error('')
        return
      }
      if (!img) {
        this.$message.error('')
        return
      }
      if (!content) {
        this.$message.error('')
        return
      }
      // console.log(this.form)
      if (id === '') {
        // 
        this.$api.post(this.$root.urlPath.MJK + '/goods', {
          name,
          img,
          content,
          categoryId: category
        }, res => {
          // console.log(res.data)
          if (res.data.code === 200) {
            this.$message({
              message: '',
              type: 'success'
            })
            this.addCategoryBox = !this.addCategoryBox
            this.$api.get(this.$root.urlPath.MJK + '/goods', {}, res => {
              // console.log(res.data)
              if (res.data.code === 200) {
                this.goods = res.data.data
              } else {
                this.$message.error(res.data.msg)
              }
            })
          } else {
            this.$message.error(res.data.msg)
          }
        })
      } else {
        // 
        this.$api.put(this.$root.urlPath.MJK + '/goods', {
          id,
          name,
          img,
          content,
          categoryId: category
        }, res => {
          // console.log(res.data)
          if (res.data.code === 200) {
            this.$message({
              message: '',
              type: 'success'
            })
            this.addCategoryBox = !this.addCategoryBox

            this.$api.get(this.$root.urlPath.MJK + '/goods', {}, res => {
              // console.log(res.data)
              if (res.data.code === 200) {
                this.goods = res.data.data
              } else {
                this.$message.error(res.data.msg)
              }
            })
          } else {
            this.$message.error(res.data.msg)
          }
        })
      }
    },
    // 
    delCategory (id) {
      let ids = []
      if (!id) {
        if (this.multipleSelection.length === 0) {
          this.$message({
            message: '',
            type: 'warning'
          })
          return
        }
        for (let i of this.multipleSelection) {
          ids.push(i._id)
        }
      } else {
        // console.log('' + id)
        ids.push(id)
      }
      console.log(ids)
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.$api.delete(this.$root.urlPath.MJK + '/goods', {
            id: ids
          }, res => {
            console.log(res.data)
            if (res.data.code === 200) {
              this.$message({
                type: 'success',
                message: '!'
              })
              this.$api.get(this.$root.urlPath.MJK + '/goods', {}, res => {
                console.log(res.data)
                if (res.data.code === 200) {
                  this.goods = res.data.data
                } else {
                  this.$message.error(res.data.msg)
                }
              })
            } else {
              this.$message.error(res.data.msg)
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    handleSelectionChange (val) {
      this.multipleSelection = val
      console.log(val)
    },
    // 
    handleSuccess (response, file, fileList) {
      console.log(response, file, fileList)
      if (response.code === 200) {
        console.log(this.fileList)
        let list = {
          name: response.data.url,
          url: response.data.url
        }
        // this.fileList.push(list)
        this.form.img.push(list)
        console.log('---------')
        console.log(this.fileList)
      } else {
        this.$message.error(response.msg)
      }
    },
    // 
    handleError (err, file, fileList) {
      console.log(err, file, fileList)
      this.$message.error('')
    },
    // 
    handleRemove (file, fileList) {
      console.log(file, fileList)
      let uid = file.uid
      let img = this.form.img
      for (let i = 0; i < img.length; i++) {
        if (img[i].uid === uid) {
          img.splice(i, 1)
        }
      }
      this.form.img = img
    },
    handlePictureCardPreview (file) {
      this.dialogVisible = true
      this.dialogImageUrl = file.url
    },
    onEditorBlur (editor) {
      console.log('editor blur!', editor)
    },
    onEditorFocus (editor) {
      console.log('editor focus!', editor)
    },
    onEditorReady (editor) {
      console.log('editor ready!', editor)
    },
    onEditorChange ({ editor, html, text }) {
      console.log('editor change!', editor, html, text)
      this.content = html
    }
  }
}
</script>

<style scoped>
.category-topBtn {
  margin: 20px 0;
}
.category-item {
  margin-bottom: 10px;
}
</style>
<style>
.el-pagination {
  margin-top: 20px;
  text-align: right;
}
.ql-editor {
  min-height: 200px;
  max-height: 400px;
  overflow-y: auto;
}
.ql-snow .ql-picker.ql-size .ql-picker-label::before,
.ql-snow .ql-picker.ql-size .ql-picker-item::before,
.ql-snow .ql-picker.ql-header .ql-picker-label::before,
.ql-snow .ql-picker.ql-header .ql-picker-item::before,
.ql-snow .ql-picker.ql-font .ql-picker-label::before,
.ql-snow .ql-picker.ql-font .ql-picker-item::before {
  position: relative;
  top: -7px;
}
</style>



