<template>
  <main class="box">
    <h2></h2>
    <div class="category-topBtn" style="margin-top: 20px">
      <el-button @click="addCategory()"></el-button>
      <el-button @click="delCategory()"></el-button>
    </div>
    <el-table
      stripe
      border
      ref="multipleTable"
      :data="tableData3"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        label="id"
        width="230">
        <template slot-scope="scope">{{ scope.row._id }}</template>
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="img"
        label="">
        <template slot-scope="scope">
          <img :src="scope.row.img" alt="" style="width: 50px;height: 50px">
        </template>
      </el-table-column>

      <el-table-column
        fixed="right"
        label=""
        width="200">
        <template slot-scope="scope">
          <el-button size="small" @click="addCategory(scope.row._id, scope.row.name, scope.row.url,scope.row.img)"></el-button>
          <el-button type="danger" size="small" @click="delCategory(scope.row._id)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      :close-on-click-modal= false
      class="addCategory-box" title="" :visible.sync="addCategoryBox">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" placeholder="" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-upload
            class="upload-demo"
            action="https://kc.hcode.xyz/api/upload"
            :on-success="handleSuccess"
            :on-error="handleError"
            :on-remove="handleRemove"
            :file-list="fileList"
            :limit="1"
            list-type="picture">
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">jpg/png500kb,390*150</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addCategoryBox = false"> </el-button>
        <el-button type="primary" @click="addSubmit"> </el-button>
      </div>
    </el-dialog>

  </main>
</template>

<script>
export default {
  scrollToTop: true,
  name: 'swiper',
  data () {
    return {
      tableData3: [], // 
      multipleSelection: [],
      addCategoryBox: false, // 
      form: {
        name: '',
        img: '',
        id: '',
        url: ''
      },
      formLabelWidth: '100px',
      fileList: []
    }
  },
  created () {
    this.$api.get(this.$root.urlPath.MJK + '/swiper', {}, res => {
      console.log(res.data)
      if (res.data.code === 200) {
        this.tableData3 = res.data.data
      } else {
        this.$message.error(res.data.msg)
      }
    })
  },
  methods: {
    // 
    addCategory (e, n, u, i) {
      this.addCategoryBox = !this.addCategoryBox
      if (!e) {
        this.form.id = ''
        this.form.name = ''
        this.form.url = ''
        this.form.img = ''
        this.fileList = []
      } else {
        this.form = {
          id: e,
          name: n,
          url: u,
          img: i
        }
        this.fileList = [
          {
            name: i,
            url: u
          }
        ]
      }
    },
    // 
    addSubmit () {
      let name = this.form.name
      let img = this.form.img
      let id = this.form.id
      if (!name) {
        this.$message.error('')
        return
      }
      if (!img) {
        this.$message.error('')
        return
      }
      if (id === '') {
        this.$api
          .post(this.$root.urlPath.MJK + '/swiper', {
            name,
            img
          },{},res => {
            this.$message({
              message: '',
              type: 'success'
            })
            // console.log(res.data)
            this.$api.get(this.$root.urlPath.MJK +'/swiper',{},res => {
              console.log(res.data)
              if (res.data.code === 200) {
                this.tableData3 = res.data.data
                this.addCategoryBox = !this.addCategoryBox
              } else {
                this.$message.error(res.msg)
              }
            })
          })
      } else {
        this.$api
          .put(this.$root.urlPath.MJK +'/swiper', {
            name,
            img,
            id
          },res => {
            // console.log(res.data)
            if (res.data.code === 200) {
              this.$message({
                message: '',
                type: 'success'
              })
              this.$api.get(this.$root.urlPath.MJK +'/swiper',{},res => {
                console.log(res.data)
                if (res.data.code === 200) {
                  this.tableData3 = res.data.data
                  this.addCategoryBox = !this.addCategoryBox
                } else {
                  this.$message.error(res.msg)
                }
              })
            } else {
              this.$message.error(res.msg)
            }
          })
      }
    },
    // 
    delCategory (id) {
      let ids = []
      if (!id) {
        if (this.multipleSelection.length === 0) {
          this.$message({
            message: '',
            type: 'warning'
          })
          return
        }
        for (let i of this.multipleSelection) {
          ids.push(i._id)
        }
      } else {
        console.log('' + id)
        ids.push(id)
      }
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.$api
            .delete(this.$root.urlPath.MJK +'/swiper', {
              params: {
                id: ids
              }
            },res => {
              // console.log(res)
              if (res.data.code === 200) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.$api.get(this.$root.urlPath.MJK +'/swiper',{},res => {
                  // console.log(res)
                  if (res.data.code === 200) {
                    this.tableData3 = res.data.data
                  } else {
                    this.$message.error(res.data.msg)
                  }
                })
              } else {
                this.$message({
                  message: res.data.msg,
                  type: 'warning'
                })
              }
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    handleSelectionChange (val) {
      this.multipleSelection = val
      console.log(val)
    },
    // 
    handleSuccess (response, file, fileList) {
      console.log(response, file, fileList)
      if (response.code === 200) {
        this.form.img = response.data.img
        this.form.url = response.data.url
      } else {
        this.$message.error(response.msg)
      }
    },
    // 
    handleError (err, file, fileList) {
      console.log(err, file, fileList)
    },
    // 
    handleRemove (file, fileList) {
      console.log(file, fileList)
      this.form.img = ''
    }
  }
}
</script>

<style scoped>
.category-topBtn {
  margin: 20px 0;
}
</style>



